<template>
    <div>
      <el-divider></el-divider>
        <el-form :inline="true">
            <el-form-item>
                <el-date-picker v-model="filter.date" type="daterange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-select v-model="filter.store" placeholder="">
                    <el-option v-for="item in filter.storeList" :key="item.deptId" :label="item.deptName" :value="item.deptId"></el-option>
                </el-select>
            </el-form-item>
            <!--el-form-item>
                <el-input v-model="filter.cust" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-checkbox v-model="filter.synced"></el-checkbox>
            </el-form-item-->
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" @click="query()"></el-button>
                    <el-button type="primary" v-loading="loading.sync" :disabled="loading.work || loading.fee || loading.post || loading.issue || loading.issueEntry" @click="sync()"></el-button>
                </el-button-group>
            </el-form-item>
            <el-progress v-if="loading.sync" :text-inside="true" :stroke-width="26" :percentage="loading.syncPercent">test</el-progress>
            <el-row>
                <el-col :span="24">
                    <el-table ref="multipleTable" v-loading="loading.work" :data="work.filter(data => (data.syncStatus || false) === filter.synced && (data.shortName && data.shortName.toLowerCase().includes(filter.cust.toLowerCase())))" @selection-change="handleSelectionChange">
                        <el-table-column prop="seled" type="selection" width="55"></el-table-column>
                        <el-table-column type="expand">
                          <template slot-scope="props">
                            <el-tabs v-model="props.row.tab">
                              <el-tab-pane label="" name="fee">
                                <el-table :data="props.row.fee" v-loading="loading.fee">
                                  <el-table-column prop="itemName" label=""></el-table-column>
                                  <el-table-column prop="quantity" label=""></el-table-column>
                                  <el-table-column prop="discountPrice" label=""></el-table-column>
                                </el-table>
                              </el-tab-pane>
                              <el-tab-pane label="" name="post" v-loading="loading.post">
                                <el-table :data="props.row.post">
                                  <el-table-column prop="partName" label=""></el-table-column>
                                  <el-table-column prop="spec" label=""></el-table-column>
                                  <el-table-column prop="codeName" label=""></el-table-column>
                                  <el-table-column prop="unit" label=""></el-table-column>
                                  <el-table-column prop="quantity" label=""></el-table-column>
                                  <el-table-column prop="discountPrice" label=""></el-table-column>
                                </el-table>
                              </el-tab-pane>
                              <el-tab-pane label="" name="issue" v-loading="loading.issue">
                                <el-table :data="props.row.issue">
                                  <el-table-column type="expand">
                                    <template slot-scope="props">
                                      <el-table :data="props.row.entry">
                                        <el-table-column prop="storeName" label=""></el-table-column>
                                        <el-table-column prop="positionName" label=""></el-table-column>
                                        <el-table-column prop="partName" label=""></el-table-column>
                                        <el-table-column prop="spec" label=""></el-table-column>
                                        <el-table-column prop="inputTime" label=""></el-table-column>
                                        <el-table-column prop="unit" label=""></el-table-column>
                                        <el-table-column prop="quantity" label=""></el-table-column>
                                        <el-table-column prop="salePrice" label=""></el-table-column>
                                        <el-table-column prop="sellingPrice" label=""></el-table-column>
                                        <el-table-column prop="salePriceTotal" label=""></el-table-column>
                                        <el-table-column prop="sellingPriceTotal" label=""></el-table-column>
                                      </el-table>
                                    </template>
                                  </el-table-column>
                                  <el-table-column prop="outputTime" label=""></el-table-column>
                                  <el-table-column prop="outputNo" label=""></el-table-column>
                                  <el-table-column prop="plateNo" label=""></el-table-column>
                                </el-table>
                              </el-tab-pane>
                            </el-tabs>
                          </template>
                        </el-table-column>
                        <el-table-column prop="firstBalanceTime" label="" width="180"></el-table-column>
                        <el-table-column prop="careNo" label="" width="150"></el-table-column>
                        <el-table-column prop="shortName" label="" width="350">
                          <template slot="header">
                            <el-input v-model="filter.cust" size="mini" placeholder=""></el-input>
                          </template>
                        </el-table-column>
                        <el-table-column prop="plateNo" label="" width="150"></el-table-column>
                        <el-table-column prop="feeAmt" label="" width="150"></el-table-column>
                        <el-table-column prop="sheetSource" label="" width="120"></el-table-column>
                        <el-table-column prop="syncStatus" label="" width="120">
                          <template slot="header">
                            <el-checkbox v-model="filter.synced" size="mini"></el-checkbox>
                          </template>
                          <template slot-scope="scope">
                            <el-checkbox disabled v-model="scope.row.syncStatus"></el-checkbox>
                          </template>
                        </el-table-column>
                        <el-table-column prop="syncTime" label="" width="220"></el-table-column>
                        <el-table-column prop="syncInfo" label="" width="280"></el-table-column>
                    </el-table>
                </el-col>
                <!--el-col :span="12">
                    <el-tabs v-model="activeName">
                        <el-tab-pane label="" name="fee"></el-tab-pane>
                        <el-tab-pane label="" name="post"></el-tab-pane>
                        <el-tab-pane label="" name="issue"></el-tab-pane>
                    </el-tabs>
                </el-col-->
            </el-row>
        </el-form>
    </div>
</template>

<script>
import Sten from '@/sten'
import K3 from '@/k3'

export default {
  name: 'StenWork',
  data () { // FSJRJL,fsjrpdigly
    return {
      filter: {
        date: '',
        store: '',
        cust: '',
        synced: false,
        storeList: []
      },
      loading: {
        fee: false,
        work: false,
        post: false,
        issue: false,
        issueEntry: false,
        querying: false,
        sync: false,
        syncPercent: 0
      },
      work: [],
      multipleSelection: [],
      activeName: 'fee',
      sten: undefined,
      k3: undefined
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    async init () {
      this.sten = new Sten()
      this.k3 = new K3()
      await this.sten.init()
      this.filter.storeList = await this.sten.deptList()
      // console.log(this.filter.storeList)
    },
    async query () {
      // console.log(this.filter.date)
      var begDate
      var endDate
      if (this.filter.date) {
        begDate = this.filter.date[0].toLocaleDateString()
        endDate = this.filter.date[1].toLocaleDateString()
      }
      this.loading.work = true
      this.loading.fee = true
      this.loading.post = true
      this.loading.issue = true
      this.loading.issueEntry = true
      try {
        var rst = await this.sten.WorkQuery(begDate, endDate, this.filter.store, this.filter.cust)
        this.$message('')
      } catch (ex) {
        this.$message.error(ex.message)
      }
      var billNoList = []
      for (var idx in rst) {
        rst[idx]['tab'] = 'fee'
        billNoList.push(rst[idx].careNo + '-' + rst[idx].plateNo)
        rst[idx]['feeAmt'] = 0
      }
      this.k3.SyncStatus(billNoList).then(syncStatus => {
        for (var idx in rst) {
          if (rst[idx].careNo + '-' + rst[idx].plateNo in syncStatus) {
            rst[idx].syncStatus = syncStatus[rst[idx].careNo + '-' + rst[idx].plateNo].syncStatus
            rst[idx].syncTime = syncStatus[rst[idx].careNo + '-' + rst[idx].plateNo].syncTime
            rst[idx].syncInfo = syncStatus[rst[idx].careNo + '-' + rst[idx].plateNo].syncInfo
          }
        }
        this.work = rst
        this.loading.work = false
        this.$message('')
      })
      // 
      this.sten.FeeInvoiceQuery(begDate, endDate, this.filter.store, this.filter.cust).then(rst => {
        var dic = {}
        for (var idx in rst) {
          var rec = rst[idx]
          if (!dic[rec.careNo]) {
            dic[rec.careNo] = []
          }
          dic[rec.careNo].push(rec)
        }
        for (idx in this.work) {
          this.work[idx].fee = dic[this.work[idx].careNo]
          for (jdx in this.work[idx].fee) {
            this.work[idx].feeAmt += this.work[idx].fee[jdx].discountPrice
          }
          this.work[idx].feeAmt = Math.round(this.work[idx].feeAmt * 100) / 100
        }
        this.loading.fee = false
        this.$message('')
      }).catch(ex => {
        this.$message.error(ex.message)
      })
      // 
      this.sten.SalePosteQuery(begDate, endDate, this.filter.store, this.filter.cust).then(rst => {
        var dic = {}
        for (var idx in rst) {
          var rec = rst[idx]
          if (!dic[rec.careNo]) {
            dic[rec.careNo] = []
          }
          dic[rec.careNo].push(rec)
        }
        for (idx in this.work) {
          this.work[idx].post = dic[this.work[idx].careNo]
        }
        this.loading.post = false
        this.$message('')
      }).catch(ex => {
        this.$message.error(ex.message)
      })
      // 
      try {
        rst = await this.sten.SaleIssueQuery(begDate, /* endDate */'2100-01-01', this.filter.store, this.filter.cust)
        var dic = {}
        for (idx in rst) {
          var rec = rst[idx]
          var key = rec.relatedNo + '|' + rec.plateNo
          if (!dic[key]) {
            dic[key] = []
          }
          dic[key].push(rec)
        }

        for (idx in this.work) {
          this.work[idx].issue = dic[this.work[idx].careNo + '|' + this.work[idx].plateNo]
        }
        this.loading.issue = false
        this.$message('')
      } catch (ex) {
        this.$message.error(ex.message)
      }
      try {
        rst = await this.sten.SaleIssueEntryQuery(begDate, /* endDate */'2100-01-01', this.filter.store, this.filter.cust)
        dic = {}
        for (idx in rst) {
          rec = rst[idx]
          if (!dic[rec.relatedNo]) {
            dic[rec.relatedNo] = {}
          }
          var cDic = dic[rec.relatedNo]
          if (!cDic[rec.outputNo]) {
            cDic[rec.outputNo] = []
          }
          dic[rec.relatedNo][rec.outputNo].push(rec)
        }
        for (idx in this.work) {
          if (this.work[idx].issue) {
            cDic = dic[this.work[idx].careNo]
            for (var jdx in this.work[idx].issue) {
              this.work[idx].issue[jdx].entry = cDic[this.work[idx].issue[jdx].outputNo]
            }
          }
          // this.work[idx].issue = dic[this.work[idx].careNo + '|' + this.work[idx].plateNo]
        }
        this.loading.issueEntry = false
        this.$message('')
      } catch (ex) {
        this.$message.error(ex.message)
      }
      // console.log('rst', this.work)
    },
    async sync () {
      this.loading.syncPercent = 0
      this.loading.sync = true
      var recs = this.multipleSelection
      for (var idx in recs) {
        var rec = recs[idx]
        var rst = await this.k3.Sync(rec)
        if (!rst.success) {
          this.$message.error(rst.info)
        }
        console.log(idx, this.multipleSelection.length)
        this.loading.syncPercent = parseInt((idx + 1) * 100 / recs.length)
        // console.log('rst', rst)
      }
      this.loading.sync = false
      this.query()
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  }
}
</script>



