<template>
    <div>
      <el-divider></el-divider>
        <el-form :inline="true">
            <el-form-item>
                <el-date-picker v-model="filter.date" type="daterange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-select v-model="filter.store" placeholder="">
                    <el-option v-for="item in filter.storeList" :key="item.deptId" :label="item.deptName" :value="item.deptId"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" @click="query()"></el-button>
                    <el-button type="primary" v-loading="loading.sync" :disabled="loading.work || loading.fee || loading.post || loading.issue || loading.issueEntry" @click="sync()"></el-button>
                </el-button-group>
            </el-form-item>
            <el-progress v-if="loading.sync" :text-inside="true" :stroke-width="26" :percentage="loading.syncPercent">test</el-progress>
            <el-row>
                <el-col :span="24">
                    <el-table ref="multipleTable" v-loading="loading.work" :data="data" @selection-change="handleSelectionChange">
                        <el-table-column prop="seled" type="selection" width="55"></el-table-column>
                        <el-table-column v-for="(v, k) in fieldDic" :key="k" :prop="k" :label="v" width="120"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
import Sten from '@/sten'
import K3 from '@/k3'

export default {
  name: 'Achievement',
  data () { // FSJRJL,fsjrpdigly
    return {
      filter: {
        date: '',
        store: '',
        cust: '',
        synced: false,
        storeList: []
      },
      loading: {
        fee: false,
        work: false,
        post: false,
        issue: false,
        issueEntry: false,
        querying: false,
        sync: false,
        syncPercent: 0
      },
      fieldDic: {
        inputNo: '',
        inputTime: '',
        relateNo: '',
        careNo: '',
        category: '',
        totalPrice: '',
        taxPrice: '',
        inputCount: '',
        buyerName: '',
        purPrice: '',
        supplierName: '',
        plateNo: '',
        modelName: '',
        vinNo: 'vin',
        deptName: ''
      },
      entryFieldDic: {
        purchaseNo: '',
        buyTime: '',
        inputNo: '',
        inputTime1: '',
        relateNo: '',
        plateNo: '',
        modelName: '',
        name: '',
        contact: '',
        tel1: '',
        groupName: '',
        partName: '',
        brandName: '',
        firmNo: 'OEM',
        partType: '',
        partNo: '',
        spec: '',
        barCode: '',
        selfNo: '',
        inputTime: '',
        storeName: '',
        positionName: '',
        supplierName: '',
        rawUnit: '',
        rawQuantity: '',
        rawBuyingPrice: '',
        buyingTotal: '',
        rawTaxPrice: '',
        taxTotal: '',
        salePrice: '',
        receiverName: '',
        purchaseStatus: '',
        applicant: '',
        buyer: '',
        handler: '',
        creator: '',
        deptName: ''
      },
      data: [],
      work: [],
      multipleSelection: [],
      activeName: 'fee',
      sten: undefined,
      k3: undefined
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    async init () {
      this.sten = new Sten()
      this.k3 = new K3()
      await this.sten.init()
      this.filter.storeList = await this.sten.deptList()
      // console.log(this.filter.storeList)
    },
    async query () {
      // console.log(this.filter.date)
      var begDate
      var endDate
      if (this.filter.date) {
        begDate = this.filter.date[0].toLocaleDateString()
        endDate = this.filter.date[1].toLocaleDateString()
      }
      this.loading.head = true
      this.loading.entry = true
      try {
        var rst = await this.sten.Achievement(begDate, endDate, this.filter.store, this.filter.cust)
        this.data = rst
        this.$message('')
      } catch (ex) {
        this.$message.error(ex.message)
      }
    },
    async sync () {
      this.loading.syncPercent = 0
      this.loading.sync = true
      var recs = this.multipleSelection
      for (var idx in recs) {
        var rec = recs[idx]
        var rst = await this.k3.Sync(rec)
        if (!rst.success) {
          this.$message.error(rst.info)
        }
        console.log(idx, this.multipleSelection.length)
        this.loading.syncPercent = parseInt((idx + 1) * 100 / recs.length)
        // console.log('rst', rst)
      }
      this.loading.sync = false
      this.query()
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  }
}
</script>



