<template>
  <div id="app">
      <el-divider>-</el-divider>
    <el-col :span="4">
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose">
      <el-menu-item v-if="user==='FSJRPDIGLY'" index="0">
        <i class="el-icon-document"></i>
        <span slot="title"><router-link :to="{name:'Test'}"></router-link></span>
      </el-menu-item>
      <el-menu-item index="1">
        <i class="el-icon-document"></i>
      <span slot="title"><router-link :to="{name:'StenWork'}"></router-link></span>
      </el-menu-item>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="2-1"><router-link :to="{name:'PurIn'}"></router-link></el-menu-item>
          <el-menu-item index="2-2"><router-link :to="{name:'PurReturn'}"></router-link></el-menu-item>
          <el-menu-item index="2-3"><router-link :to="{name:'Stocktaking'}"></router-link></el-menu-item>
          <el-menu-item index="2-4"><router-link :to="{name:'OtherIn'}"></router-link></el-menu-item>
          <el-submenu index="2-5">
            <template slot="title"></template>
            <el-menu-item index="2-5-1"><router-link :to="{name:'OtherIssue_Give'}"></router-link></el-menu-item>
            <el-menu-item index="2-5-2"><router-link :to="{name:'OtherIssue_Loss'}"></router-link></el-menu-item>
            <el-menu-item index="2-5-3"><router-link :to="{name:'OtherIssue_Tailings'}"></router-link></el-menu-item>
          </el-submenu>
          <el-menu-item index="2-6"><router-link :to="{name:'MatRet'}"></router-link></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="3-1"><router-link :to="{name:'Achievement'}"></router-link></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
    </el-col>
    <el-col :span="20">
    <router-view/>
    </el-col>
  </div>
</template>

<script>
import Sten from '@/sten'
export default {
  name: 'App',
  data () {
    return {
      sten: {},
      user: undefined
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    async init () {
      this.sten = new Sten()
      await this.sten.init()
      this.user = this.sten.user
    }
  }
}
</script>

<style>
</style>



